@charset "UTF-8";
:root {
  --font-family: "Raleway", sans-serif;
}

:root {
  --bittersweet-shimmer: hsla(355, 49%, 52%, 1);
  --maize: hsla(53, 98%, 65%, 1);
  --yellow-green: hsla(79, 54%, 51%, 1);
  --ghost-white: hsla(240, 100%, 99%, 1);
  --rich-black: hsla(203, 69%, 5%, 1);
  --light: #ffcc70;
  --lightest: #fffadd;
  --medium: #8ecddd;
  --darkest: #22668d;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  overflow: hidden;
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

@keyframes specialNodes {
  0% {
    transform: scale(0.3);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes shortestPath {
  0% {
    transform: scale(0.6);
    background-color: #fffe6a;
  }
  50% {
    transform: scale(1.2);
    background-color: #fffe6a;
  }
  100% {
    transform: scale(1);
    background-color: #fffe6a;
  }
}
.navbar {
  width: 100%;
}
.navbar__wrapper {
  background-color: var(--darkest);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 11px 0px;
  padding-block: 0.625rem;
  /* Tablet */
  /* Pc */
}
@media (min-width: 40em) {
  .navbar__wrapper {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 0.625rem;
  }
}
@media (min-width: 71.875em) {
  .navbar__wrapper {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 0.625rem;
    background-color: var(--darkest);
  }
}
.navbar-title {
  color: var(--lightest);
}
.navbar-algorithm .select-wrapper {
  position: relative;
  display: inline-block;
}
.navbar-algorithm .select-wrapper:after {
  content: "â–¼";
  position: absolute;
  top: 50%;
  right: 0.625rem;
  transform: translateY(-50%);
  pointer-events: none;
  color: var(--lightest);
}
.navbar-algorithm .select-wrapper select {
  font-size: 1rem;
  appearance: none;
  width: 12.5rem;
  background: var(--darkest);
  padding: 0.625rem;
  color: var(--lightest);
  border: 0.0625rem solid var(--light);
  border-radius: 0.25rem;
  cursor: pointer;
}
.navbar-buttons button {
  font-size: 0.9375rem;
  margin-left: 0.0625rem;
  padding: 0.625rem 1.25rem;
  color: var(--lightest);
  background-color: var(--light);
  border: none;
  border-radius: 0.25rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
.navbar-buttons button:hover {
  background-color: var(--medium);
}
.navbar-buttons button:first-child {
  margin-left: 0;
}

.grid-container {
  padding-inline: 0.3125rem;
  border-radius: 0.3125rem;
  padding-top: 0.0625rem;
  padding-bottom: 0.3125rem;
}
.grid-container table {
  border-collapse: collapse;
}

.node {
  width: 1.5625rem;
  height: 1.5625rem;
  border: 0.0625rem solid #afd9f8;
  transition: all 0.3s ease-in-out;
}
.node-start {
  transition: all 0.5s ease-in;
  background-image: url(../assets/triangletwo-right.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  animation-name: specialNodes;
  animation-duration: 0.5s;
  animation-timing-function: ease-out;
}
.node-finish {
  transition: all 0.5s ease-in;
  background-image: url(../assets/circle.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  animation-name: specialNodes;
  animation-duration: 0.5s;
  animation-timing-function: ease-out;
}
.node-wall {
  border-color: rgba(41, 40, 40, 0.774);
  background-color: #333333 !important;
  transition: all 0.5s ease-in-out;
}
.node-path {
  animation-name: shortestPath;
  animation-duration: 1s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

body {
  font-family: var(--font-family);
  background-color: var(--ghost-white);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  height: 100vh;
}